.order-history-list-button-back {
	@extend .button-back;
}
.order-history-list-header {
	border-top: 1px solid $sc-color-theme-border-light;
	padding: $sc-padding-lv3 0;
}
.order-history-list-button-back-icon {
	@extend .button-back-icon;
}
.order-history-list-empty {
	@extend .list-empty;
}

.order-history-list-recordviews-container{
	@media (min-width: $screen-md-min) {
		.recordviews-actionable-title-header,
		.recordviews-actionable-title {
			width: 20%;
		}
		.recordviews-actionable-origin-header,
		.recordviews-actionable-origin {
			width: 15%;
			text-align: left;
		}
		.recordviews-actionable-date-header,
		.recordviews-actionable-date {
			width: 15%;
			text-align: right;
		}
		.recordviews-actionable-currency-header,
		.recordviews-actionable-currency {
			width: 20%;
			text-align: right;
		}
		.recordviews-actionable-status-header,
		.recordviews-actionable-status {
			width: 25%;
			text-align: left;
		}
		.recordviews-actionable-actions-header,
		.recordviews-actionable-actions {
			width: 30%;
			text-align: right;
		}
	}
	.recordviews-actionable-status span {
		color: $sc-color-primary;
	}
}

.order-history-list-recordviews-actionable-table {
	@extend .recordviews-actionable-table;

	.recordviews-actionable-origin {
		padding-bottom:0px;
	}

	@media (min-width: $screen-sm-min) {
		.recordviews-actionable-origin {
			padding-bottom:$sc-padding-lv3;
		}
	}
}

.order-history-list-recordviews-actionable-header{
	@extend .recordviews-actionable-header;
}

.order-history-list-recordviews-actionable-title-header{
	@extend .recordviews-actionable-title-header;
}

.order-history-list-recordviews-actionable-date-header{
	@extend .recordviews-actionable-date-header;
}

.order-history-list-recordviews-actionable-currency-header{
	@extend .recordviews-actionable-currency-header;
}

.order-history-list-recordviews-actionable-status-header{
	@extend .recordviews-actionable-status-header;
}

.order-history-list-recordviews-actionable-actions-header{
	@extend .recordviews-actionable-actions-header;
}

.order-history-list-case-list-paginator{
	@extend .case-list-paginator;
}

.order-history-list-recordviews-actionable-origin-header {
	@extend .recordviews-actionable-origin-header;
}
.order-history-list-header-nav {
	margin-bottom: $sc-margin-lv4;
	position:relative;
}

.order-history-list-header-button-group {
	@extend .pill-button-group;

	a, span {
		display:table-cell;
	}
}

.order-history-list-header-button-active {
	@extend .button-medium;
	@extend .pill-button-active;
}

.order-history-list-header-button {
	@extend .button-medium;
	@extend .pill-button;
}

.order-history-list-header-button-open-active {
	@extend .order-history-list-header-button-active;
	width: auto;
}

.order-history-list-header-button-instore-active {
	@extend .order-history-list-header-button-active;
}

.order-history-list-header-button-all-active {
	@extend .order-history-list-header-button-active;
	width: auto;
}

.order-history-list-header-button-open {
	@extend .order-history-list-header-button;
}

.order-history-list-header-button-instore {
	@extend .order-history-list-header-button;
	border-left:0px;
	border-right:0px;
}

.order-history-list-header-button-all {
	@extend .order-history-list-header-button;
}



.order-history-list-empty-section {
	padding:$sc-padding-lv8 $sc-padding-lv3 $sc-padding-lv3 $sc-padding-lv3;
 	text-align:center;
}